.Hero{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100%;
    background-color: var(--colorOne);
    flex-direction:row;
}

.Hero h1{
    position: relative;
    margin: 0;
    font-size: 10vw;
}

.Hero .arrowWrapper{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1.5em;
}
.Hero .ScrollArrow{
    height: 35px;
    width: 35px;
    padding: 10px;
}

/* TEXT FADE IN ANIMATION */
.Hero h1{
    animation: fadeIn 0.8s ease-in both;
    margin: 1px;
}

.Hero h1:nth-child(1){
    animation-delay: 0.1s;
}
.Hero h1:nth-child(2){
    animation-delay: 0.9s;
}
.Hero h1:nth-child(3){
    animation-delay: 1.5s;
}
.Hero h1:nth-child(4){
    animation-delay: 2s;
}

.Hero img{
    animation: fadeIn 0.5s ease-in both;
    animation-delay: 3.5s;
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translate3d(0, -80%, 0);
    }
    to {
        opacity: 1;
        transform: translate3d(0, 0, 0);
    }
}

/*==========  Mobile First   ==========*/

/* Small Devices, Tablets */
@media only screen and (min-width : 768px) {
    .Hero h1{
        font-size: 4.5em;
    }
}

/* Medium Devices, Desktops */
@media only screen and (min-width : 992px) {
    .Hero h1{
        font-size: 5em;
    }
}

/* Large Devices, Wide Screens */
@media only screen and (min-width : 1300px) {
    .Hero h1{
        font-size: 105px;
    }
    .Hero .wrapper{
        width: 900px; 
    }
} 